var app = getApp().globalData;
const util = require("../../utils/util.js");
const userinfo = 'userinfo';
import navBar from "../../component/nav-bar/nav-bar";

export default {
  data() {
    return {
      rankData: {},
      nvabarData: {
        showCapsule: 1,
        title: '排行榜'
      }
    };
  },

  components: {
    navBar
  },
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    console.log('rank--', options);
    var that = this; // options.id = 168;

    var goUrl = decodeURIComponent(options.url) || '';

    if (goUrl != 'undefined') {
      console.log('11111--', goUrl);
      wx.request({
        url: goUrl,
        data: {
          uid: wx.getStorageSync(userinfo)['uid'] || 0
        },
        method: 'get',
        header: {
          'content-type': 'application/json' // 默认值

        },

        success(res) {
          console.log('rank1111--', res);
          that.setData({
            rankData: res.data.data
          });
        }

      });
    } else {
      wx.request({
        url: 'https://v.xueweigui.com/Words/testResultGet',
        data: {
          test_id: options.id,
          uid: wx.getStorageSync(userinfo)['uid'] || 0,
          type: getApp().globalData.g_test_type,
          stime: getApp().globalData.g_test_stime,
          etime: getApp().globalData.g_test_etime
        },
        //todo 测试
        method: 'get',
        header: {
          'content-type': 'application/json' // 默认值

        },

        success(res) {
          console.log('rank2222--', res);
          that.setData({
            rankData: res.data.data
          });
        }

      });
    }
  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {},

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {},
  methods: {}
};
